@import url(https://fonts.googleapis.com/css?family=Lato);
.link {
  text-decoration: none;
  color: inherit;
}
html,
body {
  max-width: 100%;
  overflow-x: hidden;
  margin: 0px;
}

* {
  box-sizing: border-box;
}

body {
  font-family: "Lato", sans-serif;
}

h1 {
  letter-spacing: 2px;
  text-align: center;
  text-transform: uppercase;
}

.countdown-wrapper {
  position: relative;
  top: 35vh;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap;
}

.countdown-item {
  color: #111;
  font-size: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  line-height: 30px;
  margin: 10px;
  padding-top: 10px;
  position: relative;
  width: 100px;
  height: 100px;
}

.countdown-item span {
  color: #333;
  font-size: 12px;
  font-weight: 600;
  text-transform: uppercase;
}

.countdown-svg {
  position: absolute;
  top: 0;
  left: 0;
  width: 100px;
  height: 100px;
}

.cnt-container {
  position: absolute;
  top: 0;
  width: 100vw;
  height: 100vh;
  background-image: url(/happybirthday/static/media/bg.b00c099b.png);
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  /* background-color: rgba(255, 255, 255, 0.2); */
}

.spl-txt {
  position: relative;
  top: 50vh;
  font-size: 1.8em;
  width: 100%;
  color: #111;
  text-align: center;
}

.launch {
  cursor: pointer;
  font-size: 2em;
  position: relative;
  top: 45vh;
  left: calc(50vw - 50px);
  padding: 5px 10px;
  border-radius: 5px;
  border: none;
  background-color: rgb(214, 67, 111);
  color: #eee;
}

.cont {
  background-color: #282c34;
  color: white;
  height: 100vh;
  width: 100vw;
  position: absolute;
  top: 0;
}

.grd {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-gap: 10px;
  gap: 10px;
  margin: 10px;
}

.item {
  width: 100%;
  text-align: center;
  padding: 80px 0;
  border: 2px solid rgb(180, 180, 180);
  border-radius: 10px;
  cursor: pointer;
  font-size: 1.2em;
}

p.choose {
  text-align: center;
  width: 100%;
  font-size: 1.8em;
}

a {
  color: inherit;
  text-decoration: none;
}

.me-cont {
  background-color: #282c34;
  width: 100vw;
  text-align: center;
  padding-top: 20px;
  color: white;
  font-size: 1.3em;
}

img.meme {
  width: 90vw;
  margin: 40px auto;
}

.w-cont {
  background-color: #bea68e;
  width: 100vw;
  text-align: center;
  color: rgb(10, 10, 10);
  font-size: 1.3em;
  min-height: 100vh;
}

img.bg {
  width: 100vw;
}

.mt20 {
  margin-top: 20px;
}

img.gift {
  width: 70%;
}

.cnt {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: white;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}


